.App {
  text-align: center;
}

body{
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  background-color: #0c67A0;
}

.container{
  display: flex;
  justify-content: center;
  align-items: center;
  height:100vh;
  width: 100%;
}

.form-container{
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px rgba(0, 0, 0, 0.1);
  width: 300px;
}
.form-toggle  {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}
.form-toggle button{
  background-color: #f3f3f3;
  border: none;
  cursor: pointer;
  font-size: 16px;
  border-radius:  10px 10px 0 0;
}

.form-toggle .active {
  background-color: #033452;
  color: white;

}

.change{
 border-radius: 1em;
 background-color: blue;
}

.button{
  background-color: blue;
}








.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
